.contact {
  background-image: var(--second-gradient);
}

.contact__container {
  grid-template-columns: 5fr 7fr;
  position: relative;
  z-index: 10;
}

.contact__card {
  padding-left: 130px;
  margin-bottom: 40px;
  position: relative;
}

.statusMessage {
  display: flex;
  align-items: end;
  justify-content: flex-end;
  margin-top: 30px;
  color: var(--primary-color);
  font-family: var(--body-font);
  font-size: 40px;
}

.contact__card-icon,
.contact__form-input,
.contact__form-input1,
.contact__form-input2 {
  border: 2px solid var(--border-color);
  background-color: var(--container-color);
}

.contact__card-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  box-shadow: var(--shadow);
  font-size: var(--larger-font-size);
  display: grid;
  place-items: center;
}

.contact__card-icon::before {
  content: "";
  width: 40px;
  height: 2px;
  background-color: var(--border-color);
  position: absolute;
  left: 100%;
  top: 50%;
  margin-top: -1px;
}

.contact__card-title {
  font-size: var(--h2-font-size);
  font-family: var(--body-font);
  padding-block: 6px;
}

.contact__form-group {
  grid-template-columns: repeat(1, 1fr);
  column-gap: 30px;
}

.contact__form-div {
  display: grid;
  row-gap: 10px;
  margin-bottom: 30px;
}

.contact__from-tag {
  margin-left: 30px;
  font-size: var(--smaller-font-size);
  line-height: 1.7;
  font-weight: 700;
}

.contact__from-tag b {
  color: var(--primary-color);
}

.contact__form-input {
  color: var(--tilte-color);
  height: 60px;
  border-radius: 30px;
  padding-inline: 30px;
}

.contact__form-input1 {
  color: var(--tilte-color);
  height: 60px;
  border-radius: 30px;
  padding-inline: 30px;
  width: 350px;
}

.contact__form-input2 {
  color: var(--tilte-color);
  height: 60px;
  border-radius: 30px;
  padding-inline: 30px;
  width: 350px;
}

.contact__form-area .contact__form-input {
  height: 120px;
  padding-block: 20px;
  resize: none;
}

