.header {
  position: absolute;
  top: 0;
  background-color: var();
  width: 100%;
  padding: 30px;
  z-index: 100;
}
.scroll-header{
  position: fixed;
  background-color: var(--bg-color-alt );
  animation: header-animate 0.8s var(--transition) 0s forwards;

}

@keyframes header-animate{
  0%{
    transform: translateY(-100px);
  }
  100%{
    transform: translateY(0);
  }
}
.nav {
  display: flex;
  justify-content: space-between;
}

.nav__logo {
  color: var(--tilte-color);
  font-size: var(--h4-font-size);
  font-weight: 700;
  cursor: pointer;
}

.nav__btns {
  display: flex;
  align-items: center;
  column-gap: 40px;
}

.theme__toggle {
  font-size: var(--h4-font-size);
  display: flex;
  align-items: center;

}
.theme__toggle,
.nav__toggle
{
cursor: pointer;
}


.nav__toggle {
  height: 30px;
  width: 28px;
  position: relative;
  z-index: 100;
  /* background-color:var() ; */
}
.nav__toggle span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--tilte-color);
  transition: all 0.3s var(--transition);
}
.nav__toggle span:first-child {
  top: 8px;
}
.nav__toggle span:last-child {
  bottom: 8px;
}

.nav__menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 0;
  height: 100vh;
  background-color: var(--bg-color);
  z-index: 10;
  display: flex;
  overflow: hidden;
  transition: all 1s var(--transition);
}
.show-menu {
  width: 512px;
}

.nav__data {
  padding: 80px 84px 0 154px;
  margin: auto;
  width: 100%;
}

.nav__list {
  margin-bottom: 40px;
}
.nav__list li {
  margin-bottom: 20px;
}
.nav__link {
  color: var(--tilte-color);
  font-size: var(--h5-font-size);
  font-weight: 700;
  transition: all 0.3s var(--transition);
  cursor: pointer;
}

.nav__link:hover {
  color: var(--primary-color);
}

.nav__social {
  display: flex;
  column-gap: 20px;
  margin-bottom: 30px;
}
.active,
.nav__social-link {
  color: var(--title-color);
  font-size: var(--h5-font-size);
  transition: all 0.7 var(--transition);
}

.nav__social-link:hover {
  color: var(--primary-color);
}

.animate-toggle span:first-child {
transform: rotate(-45deg);
top: 14px;
}
.animate-toggle span:last-child {
transform: rotate(45deg);
bottom: 14px;
}

.header__deco{
  left: 75px;
}

.header__deco .shape{
  top: -200px;
  left: -168px;
  z-index: -1;
}